import { useState, useEffect } from "react";

const CitySearch = ({ allLocations, setCurrentCity, setInfoAlert }) => {
    const [showSuggestions, setShowSuggestions] = useState(false);
    const [query, setQuery] = useState("");
    const [suggestions, setSuggestions] = useState([]);

    const handleInputChanged = (event) => {
        // Obtains the current value of the input field
        const value = event.target.value;
        const filteredLocations = allLocations ? allLocations.filter((location) => {
            return location.toUpperCase().indexOf(value.toUpperCase()) > -1;
        }) : [];

        // Sets the Query local state to the input value
        setQuery(value);
        setSuggestions(filteredLocations);

        // Returns a message if the list contains no suggestions
        let infoText;
        if (filteredLocations.length === 0) {
            infoText = "We cannot find the city you are looking for. Please try another city."
        } else {
            infoText = ""
        }

        setInfoAlert(infoText);
    };

    const handleItemClicked = (event) => {
        const value = event.target.textContent;
        setQuery(value);

        // Hides the list
        setShowSuggestions(false);

        // Sets current city
        setCurrentCity(value);

        // Removes InfoAlert's message
        setInfoAlert("");
    };

    // Initializes local state suggestions to have the default value as the same array as allLocations
    useEffect(() => {
        setSuggestions(allLocations);
    }, [`${allLocations}`]);

    return (
        <div id="city-search">
            <input
                type="text"
                className="city"
                placeholder="Search for a city"
                value={query}
                onFocus={() => setShowSuggestions(true)}
                onChange={handleInputChanged}
            />
            {showSuggestions ?
                <ul className="suggestions">
                    {suggestions.map((suggestion) => {
                        return <li onClick={handleItemClicked} key={suggestion}>{suggestion}</li>
                    })}
                    <li key="See all cities" onClick={handleItemClicked}>
                        <b>See all cities</b>
                    </li>
                </ul>
                : null
            }
        </div>
    )
}

export default CitySearch;
